.meals {
  align-items: center;
  background: #0fa36b;
  color: #464646;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100%;
}

.rocksGlass {
  animation: shake 1s infinite ease;
  max-height: 80vh;
}

/* Botões e Cards das Páginas de Bebidas e Comidas */
.btn-list-cards {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  justify-items: center;
}

.btn-filterMeasls-cards {
  height: 1.75rem;
  margin: 0.25rem;
  width: 7rem;
}

.btn-filterDrinks-cards {
  height: 3rem;
  margin: 0.25rem;
  width: 7.6rem;
}

